import React, { Component } from 'react';
import './App.css';
import Search from './Components/Search';
import Resultados from './Components/Resultados';
import Cabecera from './Components/Navbar';
import {db} from "../src/server";
const _ = require('lodash');

class App extends Component{

  constructor(props) {
    super(props);
    this.state = { 
    termino: '',
    documentos: [],
    docs: [],
    selected: [],
    pagina:'0'};
  }

  //enviar datos de busqueda a Recursos/Query
  EnviarQuery=()=> {
    var termino = this.state.termino;
    const pag = this.state.pagina;
    var labels = this.state.selected;
    var string = labels.value.toString();
    termino += ' ' +string.replace(/,/g, " ");;

  console.log(termino);
    const a =  db.ref("recursos/queries");
    var pushid = a.push({
      query: termino,
      pag: pag,
      etiquetas:labels })
    var clave2 = pushid.key
    return clave2
  }


  //obtener resultados de busqueda Recursos/Results
  Resultados = (id) => {
    try {

      db.ref('recursos/results/' + id).on('value',snap => {
          if (snap.exists()) {
            this._isMounted = true;
            if(snap.val().hits == null){
              this._isMounted = false;
            }
            else{
              var docswait = snap.val().hits;
            }
            console.log(docswait);
            if (this._isMounted) {
            this.setState({docs: docswait})
            }

          }
        });
    } catch (err) {
      console.log(err);
    }
  }
  //Obtiene las etiquetas seleccionadas
  getLabel = (value) => {
    this.setState({
      selected: value
     })
    console.log('Selected: ', value);
  }

  //Obtiene el termino de busqueda
  datosBusqueda = (termino) => {
    var a = performance.now()
    this.setState({
      termino: termino,
      pagina: 1
    }, ()=>{

      var id = this.EnviarQuery();
      console.log(id);
      this.Resultados(id);
     // this.consultarApi();
    })
    var b = performance.now()
    console.log("tiempo" + (b-a));

  }

  scroll =() => {
    const elemento = document.querySelector('.jumbotron');
    elemento.scrollIntoView('smooth', 'start');
  }

  //Pagina anterior
  paginaAnterior =() =>{
    //leer el state de la pag actual
    let pagina = this.state.pagina;
    // leer si la pagina es 1
    if (pagina ===1) return null;
    //restar uno a la pagina actual
    pagina-=1;
    //agregar el cambio al state
    this.setState({
      pagina
    }, () => {
      var id = this.EnviarQuery();
      this.Resultados(id);
      this.scroll();
    });
  }

  //Pagina Siguente
  paginaSiguiente=() => {
    //leer el state de la pag actual
    let pagina = this.state.pagina;
    //sumar uno a la pagina actual
    pagina+=1;
    //agregar el cambio al state
    this.setState({
      pagina
    }, () => {
      var id = this.EnviarQuery();
      this.Resultados(id);
      });
  }

  render(){
    return (
      <div className="app container">
        <Cabecera/>
        <div className ="jumbotron">
        <h2 className="text-center">REA Buscador</h2>
          <Search 
            onGetLabels={this.getLabel}
            datosBusqueda ={this.datosBusqueda}
          />
        </div>
        <div className="row justify-content-center">
          <Resultados
            docs = {this.state.docs}
            paginaAnterior = {this.paginaAnterior}
            paginaSiguiente ={this.paginaSiguiente}
          />
        </div>
      </div>

    );
    }
}

export default App;
