import React, {Component} from 'react';
import Select from 'react-select';


class Search extends Component {

      
      constructor(props) {
        super(props);
        this.state = { value: ['Infer'] };
        // bind the functions
        this.handleChange = this.handleChange.bind(this);
      }

      handleChange =(event)=> 
    {
        event.preventDefault();
        const options = event.target.options;
        var value = [];
        for (var i = 0, l = options.length; i < l; i++) {
            if (options[i].selected) {
                value.push(options[i].value);
              }
        }
        this.setState(
            { value:value });
        this.props.onGetLabels({value}); 
  
    }

    busquedaRef = React.createRef();
    obtenerDatos = (e) => { 
        e.preventDefault();
        console.log(this.busquedaRef.current.value);
        const termino = this.busquedaRef.current.value;
        this.props.datosBusqueda(termino, this.value);
    }
    
    render(){
        const { value } = this.state;
      return (
          
        <form onSubmit = {this.obtenerDatos}>
            <div className="row">
                <div className="form-group col-md-4">
                    <input ref = {this.busquedaRef} type="text" className="form-control form-control-lg" placeholder="" />
                </div>
                <div className="form-group col-md-4">
                    <select  className="form-control" data-placeholder="Select Your Options" multiple
                    onChange={this.handleChange} value = {value} >
                        <option value="identify o recognize">identify o recognize</option>
                        <option value="discriminate">discriminate</option>
                        <option value="compare">compare</option>
                        <option value="select">select</option>
                        <option value="organize">organize</option>
                        <option value="analize or analyze">analize or analyze</option>
                        <option value="infer">infer</option>
                        <option value="judge or prosecute">judge or prosecute</option>
                        <option value="represent">represent or diagram or schematize or design or graph or draw</option>
                        <option value="argue">argue</option>
                        <option value="apply or use">apply or use</option>
                        <option value="formulate or pose">formulate or pose</option>
                    </select>
                </div>
                <div className="form-group col-md-4">
                    <input type="submit" className="btn btn-lg btn-danger btn-block" placeholder="" />
                </div>
            </div>
        </form>
      );
    }
  }
  export default Search;